import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import logo from "./logo.svg";
// import "./App.css";
import Matching from "../src/pages/Interactive/Matching";

import ServerPage from "./pages/Server/ServerPage";
function App() {
  const [data, setData] = useState([]);

  return (
    <Router>
      <div className="App">
        <Routes>
          <Route path="/" element={<ServerPage />} />

          <Route path="/thinkzoneservers" element={<ServerPage />} />
          <Route
            path="/matching/:userid/:username/:usertype/:managerid/:managername/:passcode/:topicId"
            element={<Matching />}
          />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
