const renderCanvas = (
  canvas,
  leftArr,
  rightArr,
  handleSubmit,
  onend,
  setMatchedStatus,
  previewMode = false,
  inputAnswer = [],
  updateAnswerArray
) => {
  const ctx = canvas.getContext("2d");
  ctx.canvas.width = 370;
  ctx.canvas.height = 410;

  const { height, width } = ctx.canvas;
  const rowHeight = height / leftArr.length;
  const margin = nodeRadius * 8.0;

  const nodes = [
    ...leftArr.map((_, i) => ({
      id: _.id,
      type: _.type,
      value: _.left,
      x: margin,
      y: i * rowHeight + rowHeight / 3,
      side: "left",
    })),
    ...rightArr.map((_, i) => ({
      id: _.id,
      type: _.type,
      value: _.right,
      x: width - margin,
      y: i * rowHeight + rowHeight / 3,
      side: "right",
    })),
  ];

  console.log("nodes----------->", nodes);

  let edges = [];
  if (previewMode) {
    edges = inputAnswer
      .map((answer) => {
        const leftNode = nodes?.find(
          (node) => node.value === answer.left && node.side === "left"
        );
        const rightNode = nodes.find(
          (node) => node.value === answer.right && node.side === "right"
        );

        // Ensure both leftNode and rightNode exist before returning
        if (leftNode && rightNode) {
          return [leftNode, rightNode].sort((a, b) => a.x - b.x);
        }
        return null; // Return null if matching nodes are not found
      })
      .filter(Boolean); // Filter out any null values
  }
  const answerArray = [];
  let activeNode = null;
  let pointer = null;
  let isDragging = false;

  let animationRequestId = null; // Store the requestAnimationFrame ID

  const getPointerPosition = (e) => {
    const rect = e.target.getBoundingClientRect();
    return { x: e.clientX - rect.left, y: e.clientY - rect.top };
  };

  const onTouchStart = (e) => {
    const touch = e.touches[0];
    pointer = getPointerPosition(touch);
    const closestNode = findClosestNode(nodes, pointer);
    activeNode = closestNode;
    isDragging = true;

    // Start animation loop if not already running
    if (!animationRequestId) {
      animationRequestId = requestAnimationFrame(animate);
    }
  };

  const onTouchMove = (e) => {
    e.preventDefault();
    const touch = e.touches[0];
    pointer = getPointerPosition(touch);
  };

  const onTouchEnd = (e) => {
    e.preventDefault();
    isDragging = false;

    const intentRadius = nodeRadius * 2.9;
    const closestNode = findClosestNode(nodes, pointer, intentRadius);
    if (
      activeNode &&
      closestNode &&
      !equalsNode(activeNode, closestNode) &&
      !sameVertical(activeNode, closestNode)
    ) {
      const edge = [activeNode, closestNode].sort((a, b) => a.x - b.x);
      addEdge(edges, edge);

      //------------------- My code -------------------
      answerArray.push(edge);
      updateAnswerArray(answerArray);
      if (answerArray.length === leftArr.length) {
        // handleSubmit(answerArray);
        onend(answerArray);
        setMatchedStatus(true);
      }
      activeNode = null;
      console.log("len------>", answerArray, leftArr.length, edges);
      // ----------------------------------------------
    }
  };

  const animate = () => {
    ctx.fillStyle = "#f3f2ff"; // Set the desired background color
    ctx.fillRect(0, 0, ctx.canvas.width, ctx.canvas.height);

    edges.forEach(([start, end]) => {
      ctx.beginPath();
      ctx.lineWidth = 4;
      ctx.strokeStyle = previewMode ? "red" : "#0060ca";
      // ctx.strokeStyle = "#0060ca";
      ctx.moveTo(start.x, start.y);
      ctx.lineTo(end.x, end.y);
      ctx.stroke();
    });

    nodes.forEach((node) => {
      ctx.beginPath();
      ctx.arc(node.x, node.y, nodeRadius, 0, Math.PI * 2);
      ctx.fillStyle = equalsNode(node, activeNode) ? "#0060ca" : "#0060ca";
      ctx.fill();

      ctx.font = "14px Arial";
      ctx.fillStyle = "black";

      const padding = 3; // Padding between text lines
      const maxWidth = 100; // Maximum width before wrapping text
      const textMargin = nodeRadius + 12; // Margin from node to text
      const marginTop = 4; // Margin above the text
      const textMarginR = nodeRadius + 53;
      const maxCharsPerLine = 8;

      if (node.type === "text") {
        const lines = splitTextInHalf(node.value);
        lines.forEach((line, index) => {
          const textX =
            node.side === "left"
              ? node.x - maxWidth - textMargin // Place text to the left of the node
              : node.x + maxWidth - textMarginR; // Place text to the right of the node

          const textY = node.y + marginTop + index * (20 + padding);

          ctx.font = "14px Arial uppercase";
          ctx.fillStyle = "black";
          ctx.fillText(line, textX, textY);
        });
      } else if (node.type === "image") {
        if (node.side === "left") {
          ctx.font = "14px Arial";
          ctx.fillStyle = "black";
          ctx.fillText(
            node.value.substring(node.value.lastIndexOf("/") + 1),
            node.x - maxWidth - textMargin,
            node.y + nodeRadius + 20
          );
        } else if (node.side === "right") {
          ctx.font = "14px Arial uppercase";
          ctx.fillStyle = "black";
          ctx.fillText(
            node.value,
            node.x + nodeRadius * 2 + textMargin,
            node.y + nodeRadius + 20
          );
        }

        // Optional: Code for image rendering (if needed)
        // const img = new Image();
        // img.src = node.value;
        // img.onload = () => {
        //   ctx.drawImage(
        //     img,
        //     node.x + 25,
        //     node.y - nodeRadius,
        //     nodeRadius * 2,
        //     nodeRadius * 2
        //   );
        // };
      }
    });

    if (isDragging && activeNode) {
      const intentRadius = nodeRadius * 1.5;
      const closestNode = findClosestNode(nodes, pointer, intentRadius);
      const intent = closestNode || pointer;

      ctx.beginPath();
      ctx.lineWidth = 2;
      ctx.strokeStyle = "red";
      ctx.setLineDash([3, 3]);
      ctx.moveTo(activeNode.x, activeNode.y);
      ctx.lineTo(intent.x, intent.y);
      ctx.stroke();
      ctx.setLineDash([]);
    }

    // Request next animation frame if still needed
    if (isDragging || edges.length > 0) {
      animationRequestId = requestAnimationFrame(animate);
    } else {
      animationRequestId = null;
    }
  };

  // Helper function to wrap text
  const splitTextInHalf = (text) => {
    if (text.length > 8) {
      const mid = Math.ceil(text.length / 2);
      return [text.slice(0, mid), text.slice(mid)];
    }
    return [text];
  };

  // Start animation loop
  animationRequestId = requestAnimationFrame(animate);

  return { onTouchStart, onTouchMove, onTouchEnd };
};

const randomize = () => Math.random() - 0.5;

